/* Categories CSS */

.cattest{
  position: absolute;
  height: 250px;
  cursor: pointer;
  overflow: hidden;
  box-shadow: 5px 11px 10px 0px rgba(59, 36, 36, 0.5);

}

.catcards {
  margin: 20px 200px 270px 140px;
  float: left;    
}

.catimg{
  transition: all 1s ease-in;
}
.catspan{
  position: absolute;
  color: white;
  left: 30%;
  bottom: 50%;
  background-color: rgba(22, 21, 21, 0.63);
  font-weight: bolder;
  font-size: larger;
  /* padding: 0px 0px 60px 40px; */
  transform:translateY(20px);
  transition: all 1s ease-in;
  font-family:Georgia, 'Times New Roman', Times, serif;

}
.cattest:hover span{
  transform: translateY(-0px);
  font-weight: 500px;
  

}

.cattest:hover img{
  transform: scale(1.2);
  transition: all 1s ease-in;
  filter: brightness(.5);

}


.catvl{
  width: 4px;
  height: 310px;
  background-color:rgb(218, 211, 211);
  position: absolute;
  left: 20px;
  transform:translateY(0px);
  transition: all 1s ease-in;
  overflow: hidden;
}
.cattest:hover .catvl{
  transform: translateY(-310px);
  

}
.cathl{
  width: 400px;
  height: 4px;
  background-color:rgb(218, 211, 211);
  position: absolute;
  top: 20px;
  transform:translateX(-490px);
  transition: all 1s ease-in;
  overflow: hidden;
  text-align: center;
}
.cattest:hover .cathl{
  transform: translateX(0px);
  

}
.catv1{
  width: 4px;
  height: 310px;
  background-color:rgb(218, 211, 211);
  position: absolute;
  right: 20px;
  transform:translateY(-620px);
  transition: all 1s ease-in;
  overflow: hidden;
  
}
.cattest:hover .catv1{
  transform: translateY(-310px);
  

}
.cath1{
  width: 400px;
  height: 4px;
  background-color:rgb(218, 211, 211);
  position: absolute;
  bottom: 20px;
  transform:translateX(350px);
  transition: all 1s ease-in;
  overflow: hidden;
  
}
.cattest:hover .cath1{
